import React, { useEffect } from 'react'

const Page404 = () => {
  useEffect(() => {
    return (() => document.title = 'Page not found')
  }, [])

  return (
   <div className="container">
    <h1>404</h1>
    <h2>Not Found</h2>
    <p>This page doesn't exist</p>
   </div>
  )
}

export default Page404